import ApplicationController from './application_controller'

export default class extends ApplicationController {
  static targets = ["popup", "searchInput", "filterForm"]

  connect() {
    console.log("🔧 FilterPopupController connected - DEBUG VERSION")
    console.log("Targets:", {
      popup: this.hasPopupTarget,
      searchInput: this.hasSearchInputTarget,
      filterForm: this.hasFilterFormTarget
    })
    if (!this.hasPopupTarget) {
      console.error("Missing popup target")
    }
    if (!this.hasSearchInputTarget) {
      console.error("Missing searchInput target")
    }
    if (!this.hasFilterFormTarget) {
      console.error("Missing filterForm target")
    }
    
    // Test if the controller is working
    console.log("✅ FilterPopupController is connected and working")
    console.log("Element:", this.element)
    console.log("Popup target:", this.popupTarget)
    console.log("Search input target:", this.searchInputTarget)
    console.log("Filter form target:", this.filterFormTarget)
    
    // Debug: Check if club options have data-id attributes
    setTimeout(() => {
      const clubSelect = this.element.querySelector('select[name="club_shortname"]')
      if (clubSelect) {
        console.log("🔍 Debug: Club select element found:", clubSelect)
        console.log("🔍 Debug: Club select HTML:", clubSelect.outerHTML)
        for (let i = 0; i < clubSelect.options.length; i++) {
          const option = clubSelect.options[i]
          console.log(`🔍 Debug: Option ${i}:`, option.value, "data-id:", option.dataset.id)
        }
      } else {
        console.log("🔍 Debug: No club select found")
      }
    }, 1000)
    
    // Add event listeners for select fields to update ID fields
    this.setupSelectListeners()
    
    // Close popup when clicking outside
    document.addEventListener('click', this.handleClickOutside.bind(this))
    
    // Load recent selections
    this.loadRecentSelections()
  }

  disconnect() {
    console.log("FilterPopupController disconnected")
    document.removeEventListener('click', this.handleClickOutside.bind(this))
  }

  setupSelectListeners() {
    // Listen for changes on region and club select fields
    const regionSelect = this.element.querySelector('select[name="region_shortname"]')
    const clubSelect = this.element.querySelector('select[name="club_shortname"]')
    
    if (regionSelect) {
      regionSelect.addEventListener('change', (e) => this.updateIdField(e, 'region_id'))
    }
    
    if (clubSelect) {
      clubSelect.addEventListener('change', (e) => this.updateIdField(e, 'club_id'))
    }
    
    // Use MutationObserver to detect when dropdowns are updated by StimulusReflex
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.type === 'childList') {
          // Check if region dropdown was updated
          const regionSelect = this.element.querySelector('select[name="region_shortname"]')
          if (regionSelect && regionSelect.selectedIndex > 0) {
            this.updateIdField({ target: regionSelect }, 'region_id')
          }
          
          // Check if club dropdown was updated
          const clubSelect = this.element.querySelector('select[name="club_shortname"]')
          if (clubSelect && clubSelect.selectedIndex > 0) {
            this.updateIdField({ target: clubSelect }, 'club_id')
          }
        }
      })
    })
    
    // Store the current region selection to restore it after StimulusReflex updates
    this.currentRegionId = null
    
    // Observe the filter form for changes
    const filterForm = this.element.querySelector('[data-filter-popup-target="filterForm"]')
    if (filterForm) {
      observer.observe(filterForm, { childList: true, subtree: true })
    }
  }

  updateIdField(event, idFieldName) {
    const selectElement = event.target
    const selectedOption = selectElement.options[selectElement.selectedIndex]
    const idField = this.element.querySelector(`input[name="${idFieldName}"]`)
    
    if (selectedOption && idField) {
      const dataId = selectedOption.dataset.id
      console.log(`Debug: Updating ${idFieldName} field with data-id: ${dataId}`)
      idField.value = dataId || ''
    }
  }

  toggle() {
    console.log("🎯 Toggle method called - DEBUG")
    console.log("Current popup hidden state:", this.popupTarget.classList.contains('hidden'))
    
    this.popupTarget.classList.toggle('hidden')
    
    console.log("After toggle, popup hidden state:", this.popupTarget.classList.contains('hidden'))
    
    if (!this.popupTarget.classList.contains('hidden')) {
      console.log("🎯 Popup is now visible, loading recent selections...")
      this.loadRecentSelections()
      // Add a small delay to ensure the popup is fully rendered
      setTimeout(() => {
        console.log("🎯 About to call restoreCurrentSearchState...")
        try {
          this.restoreCurrentSearchState()
          console.log("🎯 restoreCurrentSearchState completed")
        } catch (error) {
          console.error("🎯 Error in restoreCurrentSearchState:", error)
        }
      }, 100)
    } else {
      console.log("🎯 Popup is now hidden")
    }
  }

  close() {
    try {
      this.popupTarget.classList.add('hidden')
    } catch (error) {
      console.error("Error in close method:", error)
    }
  }

  handleClickOutside(event) {
    if (!this.element.contains(event.target)) {
      this.close()
    }
  }

  clearFilters(event) {
    event.preventDefault()
    console.log("Clearing filters")
    this.filterFormTarget.reset()
    this.searchInputTarget.value = ''
    this.updateSearchAndRefresh('')
  }

  applyFilters(event) {
    event.preventDefault()
    console.log("Applying filters")

    const formData = new FormData(this.filterFormTarget)
    const searchParts = []
    
    // Handle global search
    const globalSearch = formData.get('global')
    if (globalSearch) {
      searchParts.push(globalSearch)
    }

    // Handle field-specific searches
    for (const [name, value] of formData.entries()) {
      if (name !== 'global' && !name.endsWith('_operator') && value && !name.endsWith('_id')) {
        const operator = formData.get(`${name}_operator`) || ''
        
        // Check if this is a reference field that should use ID-based filtering
        const isReferenceField = ['region_shortname', 'club_shortname'].includes(name)
        console.log(`Debug: Field ${name}, isReferenceField: ${isReferenceField}`)
        
        if (isReferenceField) {
          // For reference fields, use the selected option's data-id if available
          const selectElement = this.filterFormTarget.querySelector(`[name="${name}"]`)
          console.log(`Debug: Found select element for ${name}:`, selectElement)
          if (selectElement) {
            const selectedOption = selectElement.options[selectElement.selectedIndex]
            console.log(`Debug: Selected option for ${name}:`, selectedOption)
            console.log(`Debug: Selected option dataset:`, selectedOption.dataset)
            console.log(`Debug: Selected option attributes:`, selectedOption.attributes)
            
            const dataId = selectedOption.dataset.id || selectedOption.value
            
            console.log(`Debug: Field ${name}, selected value: ${selectedOption.value}, data-id: ${dataId}`)
            
            if (dataId && dataId !== '') {
              // Use ID-based field name for reference fields
              const idFieldName = name === 'region_shortname' ? 'region_id' : 'club_id'
              const searchPart = `${idFieldName}:${dataId}`
              console.log(`Debug: Adding search part: ${searchPart}`)
              searchParts.push(searchPart)
            } else {
              console.log(`Debug: No data-id found for ${name}, falling back to text search`)
              // Fall back to text-based search if no data-id
              if (operator && operator !== 'contains') {
                searchParts.push(`${name}:${operator}${value}`)
              } else {
                searchParts.push(`${name}:${value}`)
              }
            }
          }
        } else {
          // For non-reference fields, use the original logic
          if (operator && operator !== 'contains') {
            searchParts.push(`${name}:${operator}${value}`)
          } else {
            searchParts.push(`${name}:${value}`)
          }
        }
      }
    }

    const searchString = searchParts.join(' ')
    this.updateSearchAndRefresh(searchString)
    this.close()
  }

  updateSearchAndRefresh(searchString) {
    // Update the search input
    this.searchInputTarget.value = searchString
    console.log(`Debug: Updated search input with: ${searchString}`)

    // Trigger the search reflex
    const event = new Event('input', {
      bubbles: true,
      cancelable: true
    })
    this.searchInputTarget.dispatchEvent(event)
  }

  // Recent selections functionality
  saveRecentSelection(event) {
    const fieldKey = event.target.dataset.fieldKey
    const value = event.target.value
    
    if (value && fieldKey) {
      this.addToRecentSelections(fieldKey, value)
    }
  }

  addToRecentSelections(fieldKey, value) {
    const storageKey = `filter_recent_${fieldKey}`
    let recent = JSON.parse(localStorage.getItem(storageKey) || '[]')
    
    // Remove if already exists
    recent = recent.filter(item => item !== value)
    
    // Add to beginning
    recent.unshift(value)
    
    // Keep only last 5
    recent = recent.slice(0, 5)
    
    localStorage.setItem(storageKey, JSON.stringify(recent))
  }

  loadRecentSelections() {
    const fieldElements = this.element.querySelectorAll('[data-field-key]')
    
    fieldElements.forEach(fieldElement => {
      const fieldKey = fieldElement.dataset.fieldKey
      const recentContainer = fieldElement.querySelector('.recent-selections')
      
      if (recentContainer) {
        const storageKey = `filter_recent_${fieldKey}`
        const recent = JSON.parse(localStorage.getItem(storageKey) || '[]')
        
        if (recent.length > 0) {
          const recentList = recentContainer.querySelector('.flex')
          recentList.innerHTML = ''
          
          recent.forEach(value => {
            const chip = document.createElement('span')
            chip.className = 'inline-flex items-center px-2 py-1 rounded-full text-xs bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200 cursor-pointer hover:bg-blue-200 dark:hover:bg-blue-800'
            chip.textContent = value
            chip.dataset.value = value
            chip.dataset.fieldKey = fieldKey
            chip.addEventListener('click', (e) => this.selectRecentValue(e))
            recentList.appendChild(chip)
          })
          
          recentContainer.style.display = 'block'
        } else {
          recentContainer.style.display = 'none'
        }
      }
    })
  }

  restoreCurrentSearchState() {
    console.log("🔍 restoreCurrentSearchState method called")
    
    // Get the current search string from the search input
    const currentSearch = this.searchInputTarget.value
    console.log("Current search input value:", currentSearch)
    
    if (!currentSearch) {
      console.log("No current search, returning early")
      return
    }

    console.log("Restoring search state from:", currentSearch)
    
    // Debug: Check what elements are available in the form
    console.log("Form elements available:")
    const allFormElements = this.filterFormTarget.querySelectorAll('*')
    allFormElements.forEach((el, i) => {
      if (el.name) {
        console.log(`  Element ${i}: name="${el.name}", tag="${el.tagName}"`)
      }
    })
    
    // Parse the search string to extract field values
    const searchParts = currentSearch.split(' ')
    let regionId = null
    let clubId = null
    
    // First pass: collect the IDs
    searchParts.forEach(part => {
      if (part.includes(':')) {
        const [field, value] = part.split(':', 2)
        
        if (field === 'region_id') {
          regionId = value
        } else if (field === 'club_id') {
          clubId = value
        }
      }
    })
    
    console.log(`Found regionId: ${regionId}, clubId: ${clubId}`)
    
    // Second pass: restore in the correct order (region first, then club)
    if (regionId) {
      // Store the region ID to restore it after StimulusReflex updates
      this.currentRegionId = regionId
      
      // Temporarily disable StimulusReflex on region select to prevent interference
      const regionSelect = this.filterFormTarget.querySelector('select[name="region_shortname"]')
      if (regionSelect) {
        console.log("Found region select, temporarily disabling StimulusReflex")
        regionSelect.removeAttribute('data-reflex')
        regionSelect.removeAttribute('data-action')
      }
      
      this.restoreSelectValue('region_shortname', regionId, 'region_id')
      
      // Re-enable StimulusReflex and trigger the club update manually
      setTimeout(() => {
        if (regionSelect) {
          console.log("Re-enabling StimulusReflex and triggering club update")
          
          // Determine the correct reflex based on the current page
          const currentPath = window.location.pathname
          let reflexName = 'change->FilterPopupReflex#filter_clubs_by_region_for_players' // default
          
          if (currentPath.includes('/locations')) {
            reflexName = 'change->FilterPopupReflex#filter_clubs_by_region_for_locations'
          } else if (currentPath.includes('/clubs')) {
            reflexName = 'change->FilterPopupReflex#filter_clubs_by_region_for_clubs'
          }
          
          regionSelect.setAttribute('data-reflex', reflexName)
          regionSelect.setAttribute('data-action', 'change->filter-popup#saveRecentSelection change->stimulus-reflex#__perform')
          
          // Manually trigger the club dropdown update
          const event = new Event('change', { bubbles: true })
          regionSelect.dispatchEvent(event)
        }
        
        // Wait for club dropdown to be updated, then restore club
        setTimeout(() => {
          if (clubId) {
            console.log("Restoring club selection")
            this.restoreSelectValue('club_shortname', clubId, 'club_id')
          }
        }, 300)
      }, 100)
    } else if (clubId) {
      // If no region but club exists, restore club directly
      console.log("No region ID found, restoring club directly")
      this.restoreSelectValue('club_shortname', clubId, 'club_id')
    }
    
    // Handle other fields
    searchParts.forEach(part => {
      if (part.includes(':')) {
        const [field, value] = part.split(':', 2)
        
        if (field !== 'region_id' && field !== 'club_id') {
          const input = this.filterFormTarget.querySelector(`[name="${field}"]`)
          if (input) {
            input.value = value
          }
        }
      }
    })
  }

  restoreSelectValue(selectName, idValue, idFieldName) {
    // Try multiple selectors to find the elements
    let selectElement = this.filterFormTarget.querySelector(`[name="${selectName}"]`)
    let idField = this.filterFormTarget.querySelector(`[name="${idFieldName}"]`)
    
    // If not found, try alternative selectors
    if (!selectElement) {
      selectElement = this.filterFormTarget.querySelector(`select[name="${selectName}"]`)
    }
    if (!idField) {
      idField = this.filterFormTarget.querySelector(`input[name="${idFieldName}"]`)
    }
    
    console.log(`Looking for select element: [name="${selectName}"]`)
    console.log(`Looking for id field: [name="${idFieldName}"]`)
    console.log(`Found select element:`, selectElement)
    console.log(`Found id field:`, idField)
    
    if (selectElement && idField) {
      console.log(`Attempting to restore ${selectName} with data-id: ${idValue}`)
      console.log(`Available options:`, selectElement.options.length)
      
      // Find the option with matching data-id
      for (let i = 0; i < selectElement.options.length; i++) {
        const option = selectElement.options[i]
        console.log(`Option ${i}: value="${option.value}", data-id="${option.dataset.id}"`)
        if (option.dataset.id === idValue) {
          selectElement.selectedIndex = i
          idField.value = idValue
          console.log(`✅ Successfully restored ${selectName} to option with data-id: ${idValue}`)
          
          // For region selection, also trigger the change event to update club dropdown
          if (selectName === 'region_shortname') {
            console.log(`Triggering change event for region selection`)
            selectElement.dispatchEvent(new Event('change', { bubbles: true }))
          }
          break
        }
      }
    } else {
      console.log(`❌ Could not find select element for ${selectName} or id field for ${idFieldName}`)
      
      // Debug: List all form elements to see what's available
      const allFormElements = this.filterFormTarget.querySelectorAll('*')
      console.log(`All form elements:`, allFormElements.length)
      allFormElements.forEach((el, i) => {
        if (el.name) {
          console.log(`Element ${i}: name="${el.name}", tag="${el.tagName}"`)
        }
      })
      
      // Also try to find any select elements
      const allSelects = this.filterFormTarget.querySelectorAll('select')
      console.log(`All select elements:`, allSelects.length)
      allSelects.forEach((select, i) => {
        console.log(`Select ${i}: name="${select.name}", id="${select.id}"`)
      })
    }
  }

  selectRecentValue(event) {
    const value = event.target.dataset.value
    const fieldKey = event.target.dataset.fieldKey
    
    // Find the input field for this field key
    const input = this.element.querySelector(`[name="${fieldKey}"]`)
    if (input) {
      input.value = value
      input.dispatchEvent(new Event('change', { bubbles: true }))
    }
  }

  // Autocomplete functionality
  async handleAutocomplete(event) {
    const input = event.target
    const endpoint = input.dataset.endpoint
    const query = input.value
    
    if (!endpoint || query.length < 2) return
    
    try {
      const response = await fetch(`${endpoint}?q=${encodeURIComponent(query)}`)
      const data = await response.json()
      
      // Create or update autocomplete dropdown
      this.showAutocompleteDropdown(input, data)
    } catch (error) {
      console.error('Autocomplete error:', error)
    }
  }

  showAutocompleteDropdown(input, suggestions) {
    // Remove existing dropdown
    const existingDropdown = this.element.querySelector('.autocomplete-dropdown')
    if (existingDropdown) {
      existingDropdown.remove()
    }
    
    if (suggestions.length === 0) return
    
    // Create dropdown
    const dropdown = document.createElement('div')
    dropdown.className = 'autocomplete-dropdown absolute z-50 w-full bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-md shadow-lg max-h-48 overflow-y-auto'
    
    suggestions.forEach(suggestion => {
      const item = document.createElement('div')
      item.className = 'px-3 py-2 hover:bg-gray-100 dark:hover:bg-gray-700 cursor-pointer'
      item.textContent = suggestion.label || suggestion
      item.addEventListener('click', () => {
        input.value = suggestion.value || suggestion
        input.dispatchEvent(new Event('change', { bubbles: true }))
        dropdown.remove()
      })
      dropdown.appendChild(item)
    })
    
    // Position dropdown
    const rect = input.getBoundingClientRect()
    dropdown.style.top = `${rect.bottom}px`
    dropdown.style.left = `${rect.left}px`
    dropdown.style.width = `${rect.width}px`
    
    input.parentNode.appendChild(dropdown)
    
    // Close dropdown when clicking outside
    document.addEventListener('click', (e) => {
      if (!dropdown.contains(e.target) && !input.contains(e.target)) {
        dropdown.remove()
      }
    }, { once: true })
  }
}
